  display: flex;
}

.nav__list {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 635.9px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, Arial, serif;
  font-weight: 500;
